import React, { Component } from "react";
import { withRouter, Link } from "react-router-dom";
import FacebookSocialAuth from "./FacebookLogin";
import GoogleSocialAuth from "./GoogleLogin";
import { Control, Errors, LocalForm } from "react-redux-form";
import Button from "@material-ui/core/Button";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

function Alert(props) {
  console.log("herrreeeee");
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

var pass1 = "";
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const validEmail = (val) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
const validPass = (val) =>
  /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/i.test(val);
const matched = (val) => val === pass1;
const store = (val) => {
  pass1 = val;
  return true;
};

class SignUp extends Component {
  constructor() {
    super();
    this.modalRef = React.createRef();
    this.state = {
      hidden1: true,
      hidden2: true,
      open: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleShow1 = this.toggleShow1.bind(this);
    this.toggleShow2 = this.toggleShow2.bind(this);
    this.onSuccessSignup = this.onSuccessSignup.bind(this);
  }

  handleSubmit(values) {
    this.props.authSignup(
      values.username,
      values.email,
      values.password1,
      this.onSuccessSignup
    );
  }

  toggleShow1() {
    this.setState({ hidden1: !this.state.hidden1 });
  }

  toggleShow2() {
    this.setState({ hidden2: !this.state.hidden2 });
  }

  onSuccessSignup() {
    // this.setState({
    //   open: true
    // })
    this.props.history.push("/");
  }

  // handleClose() {
  //   this.setState({
  //     open: false
  //   })
  // }

  render() {
    return (
      <div className="overlay modal-wrapper fluid-container">
        {/* <Snackbar open={this.state.open}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'center',
          }}
          onClose={this.handleClose}>
          <Alert onClose={this.handleClose} severity="success">
            This is a success message!
        </Alert>
        </Snackbar> */}
        <div className="popup">
          <div className="container" style={{ padding: 20 }}>
            <div className="d-flex border-bottom">
              <h3 style={{ textAlign: "center" }}>
                <strong>Create an Account</strong>
              </h3>
              <Link
                onClick={() => {
                  this.props.history.length <= 2
                    ? this.props.history.push(this.props.prevLocation)
                    : this.props.history.push("/");
                }}
                className="ml-auto"
                style={{ color: "black" }}
              >
                <span className="fa fa-times fa-lg"></span>
              </Link>
            </div>
            <div className="border-bottom">
              <FacebookSocialAuth />
              <GoogleSocialAuth />
            </div>
            <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
              <div className="row form-group">
                <div className="col col-12">
                  <label htmlFor="username">Username</label>
                </div>
                <div className="col col-12">
                  <Control
                    type="text"
                    model=".username"
                    id="username"
                    name="username"
                    placeholder="username"
                    className="form-control"
                    validators={{
                      required,
                      minLength: minLength(3),
                      maxLength: maxLength(20),
                    }}
                  />
                  <Errors
                    className="text-danger error"
                    model=".username"
                    show="touched"
                    messages={{
                      required: "Required",
                      minLength: "Must be greater than 2 characters",
                      maxLength: "Must be 20 characters or less",
                    }}
                  />
                </div>
              </div>
              <div className="row form-group">
                <div className="col col-12">
                  <label htmlFor="email">Email</label>
                </div>
                <div className="col col-12">
                  <Control
                    type="text"
                    model=".email"
                    id="email"
                    name="email"
                    placeholder="Email"
                    className="form-control"
                    validators={{
                      required,
                      validEmail,
                    }}
                  />
                  <Errors
                    className="text-danger error"
                    model=".email"
                    show="touched"
                    messages={{
                      required: "Required ",
                      validEmail: "Invalid Email Address",
                    }}
                  />
                </div>
              </div>
              <div className="row form-group">
                <div className="col col-12">
                  <label htmlFor="password1">Password</label>
                </div>
                <div className="col col-12">
                  <div className="border password d-flex">
                    <Control
                      model=".password1"
                      id="password1"
                      name="password1"
                      placeholder="password"
                      md={10}
                      className="form-control"
                      style={{ border: "none" }}
                      type={this.state.hidden1 ? "password" : "text"}
                      validators={{
                        required,
                        validPass,
                        store,
                      }}
                    />
                    <span
                      role="button"
                      tabIndex={0}
                      onClick={this.toggleShow1}
                      onKeyDown={this.toggleShow1}
                      style={{ marginTop: 4, marginLeft: 10, outline: "none" }}
                      md={2}
                      className={
                        this.state.hidden1
                          ? "fa fa-eye pt-2 pr-2"
                          : "fa fa-eye-slash pt-2 pr-2"
                      }
                      id="togglePassword1"
                    ></span>
                  </div>
                  <Errors
                    className="text-danger error"
                    model=".password1"
                    show="touched"
                    messages={{
                      required: "Required ",
                      validPass:
                        "Must contain at least one uppercase, lowercase, numeric and special character",
                    }}
                  />
                </div>
              </div>
              <div className="row form-group">
                <div className="col col-12">
                  <label htmlFor="password2">Confirm Password</label>
                </div>
                <div className="col col-12">
                  <div className="border password d-flex">
                    <Control
                      model=".password2"
                      id="password2"
                      name="password2"
                      placeholder="Confirm Password"
                      md={10}
                      className="form-control"
                      style={{ border: "none" }}
                      type={this.state.hidden2 ? "password" : "text"}
                      validators={{
                        required,
                        matched,
                      }}
                    />
                    <span
                      role="button"
                      tabIndex={0}
                      onClick={this.toggleShow2}
                      onKeyDown={this.toggleShow2}
                      style={{ marginTop: 4, marginLeft: 10, outline: "none" }}
                      md={2}
                      className={
                        this.state.hidden2
                          ? "fa fa-eye pt-2 pr-2"
                          : "fa fa-eye-slash pt-2 pr-2"
                      }
                      id="togglePassword"
                    ></span>
                  </div>
                  <Errors
                    className="text-danger error"
                    model=".password2"
                    show="touched"
                    messages={{
                      required: "Required",
                      matched: "password does not match",
                    }}
                  />
                </div>
              </div>
              <div className="row form-group">
                <div className="col col-12">
                  <Button
                    variant="contained"
                    type="submit"
                    fullWidth={true}
                    color="primary"
                  >
                    Sign Up
                  </Button>
                </div>
              </div>
            </LocalForm>
            <div className="border-top pt-3">
              <small className="text-muted">
                Already have an Account?&nbsp;
                <Link
                  to={{
                    pathname: "/login",
                    state: { modal: true },
                  }}
                >
                  Login
                </Link>
              </small>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(SignUp);
