import React, { Component } from "react";
import { quiz } from "./quizObj";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import Checkbox from "@material-ui/core/Checkbox";
import { Button } from "@material-ui/core";
import { LocalForm } from "react-redux-form";
import TextField from "@material-ui/core/TextField";

function RenderMultipleText({ question, handleMultiplechange, questionIdx }) {
  return (
    <FormControl>
      {question.answer.options.map((ans, i) => (
        <div key={i} className="col-12">
          <FormControlLabel
            control={
              <Checkbox
                onChange={(event) => {
                  handleMultiplechange(event);
                }}
                value={ans.id}
                name={questionIdx + "_" + question.id + "_" + i}
              />
            }
            label={ans.ans}
          />
        </div>
      ))}
    </FormControl>
  );
}

function RenderMultipleImg({ question, handleMultiplechange, questionIdx }) {
  return (
    <FormControl>
      {question.answer.options.map((ans, i) => (
        <div key={i} className="col-12">
          <FormControlLabel
            control={
              <Checkbox
                onChange={handleMultiplechange}
                value={ans.id}
                name={questionIdx + "_" + question.id + "_" + i}
              />
            }
            label={<img src={ans.img} alt="" width="150px" />}
          />
        </div>
      ))}
    </FormControl>
  );
}

function RenderSingleText({ question, handleSinglechange, questionIdx }) {
  return (
    <RadioGroup aria-label={question.id} onChange={handleSinglechange}>
      {question.answer.options.map((ans, i) => {
        return (
          <div key={i} className="col-12">
            <FormControlLabel
              value={ans.id}
              key={question.id}
              name={questionIdx + "_" + question.id}
              control={<Radio />}
              label={ans.ans}
            />
          </div>
        );
      })}
    </RadioGroup>
  );
}

function RenderSingleImage({ question, handleSinglechange, questionIdx }) {
  return (
    <RadioGroup aria-label={question.id} onChange={handleSinglechange}>
      {question.answer.options.map((ans, i) => {
        return (
          <div key={i} className="col-12">
            <FormControlLabel
              value={ans.id}
              name={questionIdx + "_" + question.id}
              control={<Radio />}
              label={<img src={ans.img} alt="" width="150px" />}
            />
          </div>
        );
      })}
    </RadioGroup>
  );
}

function RenderInput({ question, handleTextInput, questionIdx }) {
  const [value, setValue] = React.useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
    handleTextInput(event.target.name, event.target.value);
  };

  return (
    <div style={{ marginLeft: 20 }}>
      <TextField
        id="standard-basic"
        rowsMax={4}
        value={value}
        name={questionIdx + "_" + question.id}
        required
        onChange={handleChange}
      />
    </div>
  );
}

function RenderQuiz({
  quiz,
  handleSinglechange,
  handleMultiplechange,
  handleTextInput,
}) {
  return (
    <div className="quiz Container">
      {quiz.questions.map((question, i) => {
        return (
          <div key={i} style={{ marginTop: 20 }}>
            <h6>{`${Number(question.id) + 1}. ` + question.question}</h6>
            {question.image ? (
              <img src={question.image} alt="" width="200px" />
            ) : null}
            {question.answer.multiple ? (
              <div>
                {question.answer.type === "text" ? (
                  <RenderMultipleText
                    questionIdx={i}
                    question={question}
                    handleMultiplechange={handleMultiplechange}
                  />
                ) : question.answer.type === "image" ? (
                  <RenderMultipleImg
                    questionIdx={i}
                    question={question}
                    handleMultiplechange={handleMultiplechange}
                  />
                ) : (
                  <RenderInput
                    questionIdx={i}
                    question={question}
                    handleTextInput={handleTextInput}
                  />
                )}
              </div>
            ) : (
              <div>
                {question.answer.type === "text" ? (
                  <RenderSingleText
                    questionIdx={i}
                    question={question}
                    handleSinglechange={handleSinglechange}
                  />
                ) : question.answer.type === "image" ? (
                  <RenderSingleImage
                    questionIdx={i}
                    question={question}
                    handleSinglechange={handleSinglechange}
                  />
                ) : (
                  <RenderInput
                    questionIdx={i}
                    question={question}
                    handleTextInput={handleTextInput}
                  />
                )}
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}

function RenderAns({ question, response }) {
  return response.map((res, i) => {
    return (
      <div key={i}>
        {i === Number(question.id) ? (
          <div>
            {question.answer.multiple ? (
              <div
                className="d-flex flex-column"
                style={{ color: res.isCorrect ? "green" : "red" }}
              >
                <h6>{`${Number(question.id) + 1}. ` + question.question}</h6>
                {question.image ? (
                  <div>
                    <img src={question.image} alt="" width="200px" />
                  </div>
                ) : null}
                <div className="d-flex flex-row">
                  <div>Ans:&nbsp;&nbsp;</div>
                  <div>
                    {res["a"] ? (
                      res["a"] === true ? (
                        question.answer.type === "text" ? (
                          <div>
                            {
                              question.answer.options["a".charCodeAt(0) - 97]
                                .ans
                            }
                          </div>
                        ) : (
                          <div>
                            <img
                              src={
                                question.answer.options["a".charCodeAt(0) - 97]
                                  .img
                              }
                              alt=""
                              width="150px"
                            />
                          </div>
                        )
                      ) : null
                    ) : null}
                    {res["b"] ? (
                      res["b"] === true ? (
                        question.answer.type === "text" ? (
                          <div>
                            {
                              question.answer.options["b".charCodeAt(0) - 97]
                                .ans
                            }
                          </div>
                        ) : (
                          <div>
                            <img
                              src={
                                question.answer.options["b".charCodeAt(0) - 97]
                                  .img
                              }
                              alt=""
                              width="150px"
                            />
                          </div>
                        )
                      ) : null
                    ) : null}
                    {res["c"] ? (
                      res["c"] === true ? (
                        question.answer.type === "text" ? (
                          <div>
                            {
                              question.answer.options["c".charCodeAt(0) - 97]
                                .ans
                            }
                          </div>
                        ) : (
                          <div>
                            <img
                              src={
                                question.answer.options["c".charCodeAt(0) - 97]
                                  .img
                              }
                              alt=""
                              width="150px"
                            />
                          </div>
                        )
                      ) : null
                    ) : null}
                    {res["d"] ? (
                      res["d"] === true ? (
                        question.answer.type === "text" ? (
                          <div>
                            {
                              question.answer.options["d".charCodeAt(0) - 97]
                                .ans
                            }
                          </div>
                        ) : (
                          <div>
                            <img
                              src={
                                question.answer.options["d".charCodeAt(0) - 97]
                                  .img
                              }
                              alt=""
                              width="150px"
                            />
                          </div>
                        )
                      ) : null
                    ) : null}
                  </div>
                </div>
              </div>
            ) : (
              <div style={{ color: res.isCorrect ? "green" : "red" }}>
                <h6>{`${Number(question.id) + 1}. ` + question.question}</h6>
                {question.image ? (
                  <div>
                    <img src={question.image} alt="" width="200px" />
                  </div>
                ) : null}
                Ans:&nbsp;&nbsp;
                {question.answer.type === "text" ? (
                  question.answer.options[res.correct.charCodeAt(0) - 97].ans
                ) : question.answer.type === "image" ? (
                  <img
                    src={
                      question.answer.options[res.correct.charCodeAt(0) - 97]
                        .img
                    }
                    alt=""
                    width="150px"
                  />
                ) : (
                  res.correct
                )}
              </div>
            )}
            {res.isCorrect ? (
              <div style={{ marginTop: 15 }}>
                Explaination: {question.answer.explanation}
              </div>
            ) : null}
          </div>
        ) : null}
      </div>
    );
  });
}

function RenderResponse({ quiz, response, score }) {
  return (
    <div className="quiz Container">
      <div style={{ backgroundColor: "#06bfad", color: "white", padding: 10 }}>
        <h3>Congratulations!!</h3>
        <h6>Your score is {score}</h6>
      </div>
      {quiz.questions.map((question, i) => {
        return (
          <div key={i}>
            <div style={{ marginTop: 20 }}>
              <div className="answer">
                {<RenderAns question={question} response={response} />}
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}

class QuizComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      quiz: this.props.quiz,
      response: new Array(quiz.questions.length),
      answers: new Array(quiz.questions.length),
      submitted: false,
      score: 0,
    };

    this.handleSinglechange = this.handleSinglechange.bind(this);
    this.handleMultiplechange = this.handleMultiplechange.bind(this);
    this.checkAns = this.checkAns.bind(this);
    this.handleTextInput = this.handleTextInput.bind(this);
  }

  handleSinglechange(e) {
    let [questionIdx, questionId] = e.target.name.split("_");
    const ans = this.state.answers;
    ans[Number(questionIdx)] = { id: questionId, answer: e.target.value };
    this.setState({
      answers: ans,
    });
  }

  checkSelected(element) {
    return typeof element != "boolean";
  }

  handleMultiplechange(e) {
    let [questionIdx, questionId, optionIdx] = e.target.name.split("_");
    const correct = this.state.response[Number(questionIdx)]
      ? this.state.response[Number(questionIdx)]
      : [false, false, false, false];
    console.log("correct before", optionIdx, correct);
    if (e.target.checked) {
      correct[optionIdx] = [true, e.target.value];
    } else {
      correct[optionIdx] = false;
    }
    console.log("correct after", optionIdx, correct);
    const res = this.state.response;
    res[Number(questionIdx)] = correct;
    this.setState({
      response: res,
    });
    let ansArray = [];
    this.state.response[Number(questionIdx)]
      .filter(this.checkSelected)
      .map((element) => {
        ansArray.push(Number(element[1]));
        return null;
      });
    let ans = this.state.answers;
    ans[Number(questionIdx)] = { id: questionId, answer: ansArray };
    this.setState({
      answers: ans,
    });
    console.log(this.state.answers);
  }

  handleTextInput(name, answer) {
    let [questionIdx, questionId] = name.split("_");
    const ans = this.state.answers;
    ans[Number(questionIdx)] = { id: questionId, answer: answer };
    this.setState({
      answers: ans,
    });
  }

  checkAns() {
    console.log(this.state.answers);
    this.setState({
      submitted: true,
    });
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }

  render() {
    return (
      <div
        className="quiz container"
        style={{ marginTop: 60, marginBottom: 40, paddingLeft: 50 }}
      >
        <div className="row justify-content-center">
          <div className="col-12">
            <h4>{quiz.title}</h4>
            {!this.state.submitted ? (
              <LocalForm onSubmit={this.checkAns}>
                <RenderQuiz
                  handleSinglechange={this.handleSinglechange}
                  handleTextInput={this.handleTextInput}
                  handleMultiplechange={this.handleMultiplechange}
                  quiz={this.state.quiz}
                />
                <hr />
                <div className="row">
                  <Button
                    style={{ marginTop: 20 }}
                    type="submit"
                    className="cyan ml-auto"
                    variant="contained"
                  >
                    submit
                  </Button>
                </div>
              </LocalForm>
            ) : (
              <div>
                <RenderResponse
                  quiz={this.state.quiz}
                  response={this.state.response}
                  score={this.state.score}
                />
                <Button
                  style={{ marginTop: 20 }}
                  className="cyan"
                  variant="contained"
                  onClick={() => {
                    this.props.next();
                    this.props.quizSubmission();
                  }}
                >
                  Next
                </Button>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default QuizComponent;
