import * as actionTypes from "./actionTypes";

export const authStart = () => ({
  type: actionTypes.AUTH_START,
});

export const authSuccess = (token) => ({
  type: actionTypes.AUTH_SUCCESS,
  token: token,
});

export const signupSuccess = () => ({
  type: actionTypes.SIGNUP_SUCCESS,
  msg: "Email verification link has been sent to your email",
});

export const authFail = (err) => ({
  type: actionTypes.AUTH_FAIL,
  error: err,
  token: null,
});

export const signupFail = () => ({
  type: actionTypes.SIGNUP_FAIL,
});

export const logout = () => {
  localStorage.removeItem("token");
  localStorage.removeItem("expirationDate");
  return {
    type: actionTypes.AUTH_LOGOUT,
  };
};

export const authLogin = (
  username,
  password,
  pathname,
  onSuccessLogin,
  onFailedLogin
) => (dispatch) => {
  dispatch(authStart());
  return fetch("https://robofreaks-be.herokuapp.com/api/users/login", {
    method: "POST",
    body: JSON.stringify({
      login: username,
      password: password,
    }),
    headers: {
      "Content-Type": "application/json",
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        console.log(error);
        throw error;
      }
    )
    .then((res) => res.json())
    .then((res) => {
      if (res.token !== undefined) {
        const token = res.token;
        localStorage.setItem("token", token);
        dispatch(authSuccess(token));
        onSuccessLogin(pathname);
      } else {
        var error = new Error("Error " + res.status);
        error.res = res;
        throw error;
      }
    })
    .catch((err) => {
      console.log(err);
      dispatch(authFail(err));
      onFailedLogin();
    });
};

export const authLogout = () => (dispatch) => {
  dispatch(logout());
};

export const authSignup = (username, email, password, onSuccessSignup) => (
  dispatch
) => {
  dispatch(authStart());
  return fetch("https://robofreaks-be.herokuapp.com/api/users/register", {
    method: "POST",
    body: JSON.stringify({
      username: username,
      email: email,
      password: password,
    }),
    headers: {
      "Content-Type": "application/json",
    },
  })
    .then((res) => {
      if (res.ok) {
        dispatch(signupSuccess());
        onSuccessSignup();
      }
    })
    .catch((err) => {
      console.log(err);
      dispatch(signupFail(err));
    });
};

export const authCheck = () => (dispatch) => {
  const token = localStorage.getItem("token");
  if (token === undefined || token === null) {
    console.log("heeeerrrrrrrrrrrreeeeeeeeeeee");
    dispatch(logout());
  } else {
    dispatch(authSuccess(token));
  }
};

export const verifyEmail = (user_id, timestamp, signature) => (dispatch) => {
  const creds = JSON.stringify({
    user_id: user_id,
    timestamp: timestamp,
    signature: signature,
  });
  return fetch(
    "https://robofreaks-be.herokuapp.com/api/users/verify-registration",
    {
      method: "POST",
      body: creds,
      headers: {
        "Content-Type": "application/json",
      },
    }
  )
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        console.log(error);
        throw error;
      }
    )
    .then((res) => res.json())
    .then((res) => {
      if (res.token !== undefined) {
        const token = res.token;
        localStorage.setItem("token", token);
        dispatch(authSuccess(token));
      } else {
        var error = new Error("Error " + res.status);
        error.res = res;
        throw error;
      }
    })
    .catch((err) => {
      console.log(err);
      dispatch(authFail(err));
    });
};

