function RenderForm(props) {
  return (
    <Dialog
      className="editModal"
      open={props.open}
      onClose={props.toggleModal}
      scroll="body"
      aria-labelledby="scroll-dialog-title"
      aria-describedby="scroll-dialog-description"
    >
      <div className="d-flex flex-row">
        <DialogTitle id="scroll-dialog-title">Edit Profile</DialogTitle>
        <div className="ml-auto">
          <DialogActions>
            <Button style={{ backgroundColor: "white", outline: "none" }}>
              <CloseIcon
                onClick={props.toggleModal}
                style={{ backgroundColor: "white", margin: 10 }}
              />
            </Button>
          </DialogActions>
        </div>
      </div>
      <DialogContent id="scroll-dialog-description">
        <LocalForm onSubmit={(values) => props.handleEdit(values)}>
          <div className="row form-group">
            <div className="col col-12">
              <label htmlFor="first_name">First Name</label>
            </div>
            <div className="col col-12">
              <Control
                type="text"
                model=".first_name"
                id="first_name"
                name="first_name"
                placeholder="first_name"
                className="form-control"
                validators={{
                  required,
                }}
              />
              <Errors
                className="text-danger error"
                model=".first_name"
                show="touched"
                messages={{
                  required: "Required",
                }}
              />
            </div>
          </div>
          <div className="row form-group">
            <div className="col col-12">
              <label htmlFor="last_name">Last Name</label>
            </div>
            <div className="col col-12">
              <Control
                type="text"
                model=".last_name"
                id="last_name"
                name="last_name"
                placeholder="last_name"
                className="form-control"
                validators={{
                  required,
                }}
              />
              <Errors
                className="text-danger error"
                model=".last_name"
                show="touched"
                messages={{
                  required: "Required",
                }}
              />
            </div>
          </div>
          <div className="row form-group">
            <div className="col col-12">
              <label htmlFor="organization">Organization</label>
            </div>
            <div className="col col-12">
              <Control
                type="text"
                model=".organization"
                id="organization"
                name="organization"
                placeholder="organization"
                className="form-control"
                validators={{
                  required,
                }}
              />
              <Errors
                className="text-danger error"
                model=".organization"
                show="touched"
                messages={{
                  required: "Required",
                }}
              />
            </div>
          </div>
          <div className="row form-group">
            <div className="col col-12">
              <label htmlFor="country">Country</label>
            </div>
            <div className="col col-12">
              <Control
                type="text"
                model=".country"
                id="country"
                name="country"
                placeholder="country"
                className="form-control"
                validators={{
                  required,
                }}
              />
              <Errors
                className="text-danger error"
                model=".country"
                show="touched"
                messages={{
                  required: "Required",
                }}
              />
            </div>
          </div>
          <div className="row form-group">
            <div className="col col-12">
              <label htmlFor="state">State</label>
            </div>
            <div className="col col-12">
              <Control
                type="text"
                model=".state"
                id="state"
                name="state"
                placeholder="state"
                className="form-control"
                validators={{
                  required,
                }}
              />
              <Errors
                className="text-danger error"
                model=".state"
                show="touched"
                messages={{
                  required: "Required",
                }}
              />
            </div>
          </div>
          <div className="row form-group">
            <div className="col col-12">
              <Button
                variant="contained"
                type="submit"
                fullWidth={true}
                color="primary"
              >
                Edit
              </Button>
            </div>
          </div>
        </LocalForm>
      </DialogContent>
    </Dialog>
  );
}

const required = (val) => val && val.length;

class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
  }

  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen,
    });
  }

  componentDidMount() {
    this.props.fetchProfile();
    this.props.fetchMyCourses();
  }

  handleEdit(values) {
    this.props.editProfile(
      values.first_name,
      values.last_name,
      values.organization,
      values.country,
      values.state
    );
    this.toggleModal();
  }

  render() {
    return (
      <div className="container-fluid profile">
        <RenderForm
          open={this.state.isModalOpen}
          toggleModal={this.toggleModal}
          handleEdit={this.handleEdit}
        />
        <div className="row justify-content-center">
          <div className="col-12 col-md-4 align-self-center">
            <div className="row justify-content-center profile-container">
              <div className="profile-box">
                <img alt="" src={girl} className="profile-pic" />
              </div>
            </div>
          </div>
          <div className="col-12 col-md-8">
            <div className="row justify-content-start user-info">
              <div className="col-12 col-md-8 ">
                <div className="username">
                  <h1>
                    {this.props.profile.profile
                      ? this.props.profile.profile.username
                      : this.props.profile.profile}
                  </h1>
                </div>
                <div className="username">
                  <h6>
                    <span className="fa fa-user" style={{ padding: 5 }}></span>
                    sggs institute of engineering and Technology
                  </h6>
                </div>

                <div className="row mt-4 robocoin">
                  <img src={coin} alt="robocoin" fluid />
                  <h6 className="ml-3 mt-3"> 0 Robocoins</h6>
                </div>
              </div>
              <div className="col-12 col-md-4 f-right align-items-center">
                <div className="row justify-content-center">
                  <Button
                    onClick={this.toggleModal}
                    className="m-3 button"
                    variant="contained"
                  >
                    <span
                      className="fa fa-pencil"
                      style={{ padding: 5 }}
                    ></span>
                    Edit Profile
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="row justify-content-center" style={{ marginTop: 10 }}>
          <div className="col-12 col-md-4 align-self-start">
            <div className="row justify-content-center">
              <div
                className="about text-left"
                style={{ backgroundColor: "#f6f3f1", margin: 2 }}
              >
                <Card>
                  <CardHeader
                    title="ABOUT ME"
                    style={{ backgroundColor: "#f6f3f1" }}
                  />
                  <CardContent style={{ backgroundColor: "white" }}>
                    <ul className="list-unstyled">
                      <li>
                        {this.props.profile.profile
                          ? this.props.profile.profile.first_name
                          : null}
                        &nbsp;
                        {this.props.profile.profile
                          ? this.props.profile.profile.last_name
                          : null}
                      </li>
                      <li>
                        <span
                          style={{ padding: 5 }}
                          className="fa fa-envelope"
                        ></span>
                        {this.props.profile.profile
                          ? this.props.profile.profile.email
                          : null}
                      </li>
                      <li>
                        <span
                          style={{ padding: 5 }}
                          className="fa fa-map-marker"
                        ></span>
                        {this.props.profile.profile
                          ? this.props.profile.profile.state
                          : null}
                        {","}&nbsp;
                        {this.props.profile.profile
                          ? this.props.profile.profile.country
                          : null}
                      </li>
                    </ul>
                    <Scrol to="Courses" smooth={true} duration={1000}>
                      <div className="d-flex flex-row">
                        <span
                          style={{ paddingRight: 5 }}
                          className="fa fa-angle-right fa-lg"
                        />
                        <h6>My Courses</h6>
                      </div>
                    </Scrol>
                    <Scrol to="Projects" smooth={true} duration={1000}>
                      <div className="d-flex flex-row">
                        <span
                          style={{ paddingRight: 5 }}
                          className="fa fa-angle-right fa-lg"
                        />
                        <h6>My Projects</h6>
                      </div>
                    </Scrol>
                  </CardContent>
                </Card>
              </div>
            </div>
          </div>
          <div className="col-12 col-md-8">
            <div className="row justify-content-center pr-4">
              <div id="Courses"></div>
              <h4 style={{ marginTop: 65 }}>My Courses</h4>
              <div className="row mt-3 p-2 justify-content-center">
                <div className="col-12 border-top m-1">
                  <RenderCourses />
                </div>
                <div className="col-12 m-1">
                  <RenderCourses />
                </div>
                <div className="col-12 m-1">
                  <RenderCourses />
                </div>
              </div>
              <div id="Projects"></div>
              <h4 style={{ marginTop: 65 }}>My Projects</h4>
              <div className="row mt-3 border-top p-2 justify-content-center">
                <div className="col-12 m-1">
                  <RenderProjects />
                </div>
                <div className="col-12 m-1">
                  <RenderProjects />
                </div>
                <div className="col-12 m-1">
                  <RenderProjects />
                </div>
              </div>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    );
  }
}

export default Profile;
