import React from "react";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";

import AI from "../public/icons/AI";
import Android from "../public/icons/Android";
import PythonIcon from "../public/icons/PythonIcon";
import BotIcon from "../public/icons/BotIcon";
import CIcon from "../public/icons/CIcon";
import WebIcon from "../public/icons/WebIcon";

import "./css/Workshop.css";
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-force-tabpanel-${index}`}
      aria-labelledby={`scrollable-force-tabpanel-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-force-tab-${index}`,
    "aria-controls": `scrollable-force-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: "100%",
  },
}));

export default function Workshop(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  const Items = props.workshops.map((e) => {
    const temparr = [];
    e.points.forEach((item, i) => temparr.push(<li key={item + i}>{item}</li>));
    return (
      <TabPanel
        value={value}
        index={e.id}
        key={e.id}
        style={{ overflow: "hidden" }}
      >
        {/* <div className={classes.root}></div> */}
        <Grid
          container
          spacing={3}
          className="poster shadow-box-example"
          style={{ borderRadius: "10px" }}
        >
          <Grid
            item
            xs
            md={6}
            style={{ marginBottom: "3%", paddingLeft: "2%" }}
          >
            <h2 className="Montserrat-Bold"> {e.name} </h2>
            <hr></hr>
            <ul className="pink-dot">{temparr}</ul>
            <Button className="btn-light">Enroll Now</Button>
            <Button className="btn-flat" variant="contained" color="secondary">
              Middle
            </Button>
          </Grid>
          <Grid
            item
            md={6}
            className="robo"
            alignItems="flex-start"
            style={{ alignSelf: "center" }}
          >
            <img width={390} height={400} src={e.image} alt=".." />
          </Grid>
        </Grid>
      </TabPanel>
    );
  });

  return (
    <div
      className="container"
      id="workshop"
      style={{ marginBottom: "4%", marginTop: "5%" }}
    >
      <div className={classes.root} style={{ boxShadow: "none !important" }}>
        <AppBar
          position="static"
          color="default"
          ref={(el) => {
            if (el) {
              el.style.setProperty("box-shadow", "none", "important");
            }
          }}
        >
          <Tabs
            value={value}
            onChange={handleChange}
            indicatorColor="primary"
            scrollButtons="on"
            textColor="primary"
            variant="scrollable"
            aria-label="scrollable force tabs example"
          >
            <Tab
              label="Android"
              icon={<Android />}
              {...a11yProps(0)}
              style={{ margin: "2%" }}
            />
            <Tab
              label="Python"
              icon={<PythonIcon />}
              {...a11yProps(1)}
              style={{ margin: "2%" }}
            />
            <Tab
              label="C++"
              icon={<CIcon />}
              {...a11yProps(2)}
              style={{ margin: "2%" }}
            />
            <Tab
              label="AI"
              icon={<AI />}
              {...a11yProps(3)}
              style={{ margin: "2%" }}
            />
            <Tab
              label="Web Development"
              icon={<WebIcon />}
              {...a11yProps(4)}
              style={{ margin: "2%" }}
            />
            <Tab
              label="Robotics"
              icon={<BotIcon />}
              {...a11yProps(5)}
              style={{ margin: "2%" }}
            />
          </Tabs>
        </AppBar>
        <SwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={value}
          onChangeIndex={handleChangeIndex}
        >
          {Items}
        </SwipeableViews>
      </div>
    </div>
  );
}
