import React, { Component } from "react";
import { Switch, Route, withRouter, Redirect } from "react-router-dom";
import HomeComponent from "./HomeComponent";
import Login from "./Login";
import SignUp from "./SignUp";
import CoursePage from "./CoursePage";
import WinnersComponent from "./WinnersComponent";
import CompetitionComponent from "./CompetitionComponent";
import VerifyMail from "./verifyMail";
import Header from "./Header";
import { connect } from "react-redux";
import {
  authCheck,
  authLogin,
  authSignup,
  verifyEmail,
  authLogout,
  googleLogin,
  facebookLogin,
} from "../redux/actions/auth";
import { fetchAllCourses } from "../redux/actions/course";
import ProtectedRoute from "./Protected";
import Profile from "./Profile";
import CourseDetail from "./CourseDetail";
import { Coursesobj } from "./Coursesobj";
import Learn from "./Learn";
import StudentDashboard from "./StudentDashboard";

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    courses: state.courses,
  };
};

const mapDispatchToProps = (dispatch) => ({
  authCheck: () => dispatch(authCheck()),
  authLogin: (username, password, pathname, onSuccessLogin, onFailedLogin) =>
    dispatch(
      authLogin(username, password, pathname, onSuccessLogin, onFailedLogin)
    ),
  googleLogin: (access_token, onSuccessLogin, onFailedLogin) =>
    dispatch(googleLogin(access_token, onSuccessLogin, onFailedLogin)),
  facebookLogin: (access_token, onSuccessLogin, onFailedLogin) =>
    dispatch(facebookLogin(access_token, onSuccessLogin, onFailedLogin)),
  authSignup: (username, email, password, onSuccessSignup) =>
    dispatch(authSignup(username, email, password, onSuccessSignup)),
  verifyEmail: (user_id, timestamp, signature) =>
    dispatch(verifyEmail(user_id, timestamp, signature)),
  authLogout: () => dispatch(authLogout()),
  fetchAllCourses: () => dispatch(fetchAllCourses()),
});

class MainComponent extends Component {
  constructor(props) {
    super(props);
    this.previousLocation = this.props.location;
    this.state = {
      coursesobj: Coursesobj,
    };
  }

  componentDidMount() {
    this.props.authCheck();
    this.props.fetchAllCourses();
  }

  componentDidUpdate() {
    let { location } = this.props;

    if (!(location.state && location.state.modal)) {
      this.previousLocation = location;
    }
  }

  render() {
    const { location } = this.props;
    const isModal =
      location.state &&
      location.state.modal &&
      this.previousLocation !== location;

    const CourseDetailwithId = ({ match }) => {
      return (
        <CourseDetail
          course={
            this.state.coursesobj.filter(
              (course) => course.id === parseInt(match.params.id, 10)
            )[0]
          }
        />
      );
    };
    const Head = withRouter(Header);
    return (
      <React.Fragment>
        <Head
          isAuthenticated={this.props.auth.token !== null}
          auth={this.props.auth}
          authLogout={this.props.authLogout}
        />
        <Switch location={isModal ? this.previousLocation : location}>
          <Route
            exact
            path="/home"
            component={() => <HomeComponent auth={this.props.auth} />}
          />
          <Route exact path="/competition" component={CompetitionComponent} />
          <Route exact path="/courses" component={() => <CoursePage />} />
          <Route path="/courses/:id" component={CourseDetailwithId} />
          <Route exact path="/winners" component={WinnersComponent} />

          <Route exact path="/login">
            <Login
              auth={this.props.auth}
              isModal={isModal}
              prevLocation={this.previousLocation}
              authLogin={this.props.authLogin}
              googleLogin={this.props.googleLogin}
              facebookLogin={this.props.facebookLogin}
            />
          </Route>
          <Route exact path="/signup">
            <SignUp
              auth={this.props.auth}
              isModal={isModal}
              prevLocation={this.previousLocation}
              authSignup={this.props.authSignup}
            />
          </Route>
          <Route
            exact
            path="/verify-email"
            component={() => (
              <VerifyMail
                auth={this.props.auth}
                verifyEmail={this.props.verifyEmail}
              />
            )}
          />
          <ProtectedRoute
            exact={true}
            path="/profile"
            component={Profile}
            auth={this.props.auth}
          />
          <ProtectedRoute
            exact={true}
            path="/learn/:id"
            component={Learn}
            auth={this.props.auth}
          />
          <Redirect to="/home" />
        </Switch>
        {isModal ? (
          <Switch>
            <Route exact path="/login">
              <Login
                auth={this.props.auth}
                isModal={isModal}
                prevLocation={this.previousLocation}
                authLogin={this.props.authLogin}
                googleLogin={this.props.googleLogin}
                facebookLogin={this.props.facebookLogin}
              />
            </Route>
            <Route exact path="/signup">
              <SignUp
                auth={this.props.auth}
                isModal={isModal}
                prevLocation={this.previousLocation}
                authSignup={this.props.authSignup}
              />
            </Route>
          </Switch>
        ) : null}
      </React.Fragment>
    );
  }
}
export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(MainComponent)
);
