import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import logo from "../public/robofreaks.png";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const NavItem = (props) => {
  const pageURI = window.location.pathname + window.location.search;
  const liClassName = props.path === pageURI ? "nav-item active" : "nav-item";
  // const aClassName = props.disabled ? "nav-link disabled" : "nav-link"
  if (pageURI == "home") {
    console.log("home");
  }
  return (
    <li className={liClassName}>
      <Link className="nav-link" to={props.path}>
        {props.name}
        {props.path === pageURI ? (
          <span className="sr-only">(current)</span>
        ) : (
          ""
        )}
      </Link>
    </li>
  );
};

class Header extends Component {
  constructor(props) {
    super(props);
    this.toggleNav = this.toggleNav.bind(this);
    this.reportWindowSize = this.reportWindowSize.bind(this);
    this.changenavbg = this.changenavbg.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleOpen = this.handleOpen.bind(this);
    this.state = {
      isNavOpen: false,
      ismyNavopen: false,
      isAuthenticated: this.props.auth.token !== null,
      open: true,
    };
  }

  changenavbg() {
    if (window.scrollY >= 35) {
      document.querySelector(".navbar").className =
        "navbar fixed-top navbar-expand-lg navbar-light bg-light scrol";
    } else {
      document.querySelector(".navbar").className =
        "navbar fixed-top navbar-expand-lg navbar-light bg-light";
    }
  }

  toggleNav() {
    this.setState({
      isNavOpen: !this.state.isNavOpen,
    });
  }

  handleOpen() {
    this.setState({
      open: true,
    });
  }

  handleClose() {
    this.setState({
      open: false,
    });
  }

  componentDidMount() {
    window.addEventListener("scroll", this.changenavbg);
    window.addEventListener("resize", this.reportWindowSize);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.changenavbg);
    window.removeEventListener("resize", this.reportWindowSize);
  }

  reportWindowSize() {
    if (window.innerWidth < 1003) {
      this.setState({
        isNavOpen: false,
      });
    }
  }

  render() {
    if (this.state.ismyNavopen) {
      document.querySelector("body").className = "prevscol";
    } else {
      document.querySelector("body").className = "";
    }
    const pageURI = window.location.pathname + window.location.search;
    return (
      <div
        className={
          this.state.ismyNavopen
            ? "prevscol"
            : "" + pageURI == "/home"
            ? "hom"
            : ""
        }
      >
        {
          // console.log()
          this.props.auth.signup ? (
            <Snackbar
              open={this.state.open}
              anchorOrigin={{
                vertical: "top",
                horizontal: "center",
              }}
              onClose={this.handleClose}
            >
              <Alert onClose={this.handleClose} severity="success">
                {this.props.auth.msg}
              </Alert>
            </Snackbar>
          ) : null
        }
        <nav
          className={"navbar fixed-top navbar-expand-lg navbar-light bg-light"}
        >
          <Link className="navbar-brand" to="/home">
            <img
              alt=""
              src={logo}
              width="35"
              height="35"
              className="d-inline-block align-top mr-1"
            />
            <strong className="Montserrat-Bold">Robo</strong>
            <small style={{ fontSize: "1.25rem" }}>Freaks</small>
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
            onClick={() =>
              this.setState({ ismyNavopen: !this.state.ismyNavopen })
            }
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div
            className="collapse navbar-collapse"
            id="navbarNav"
            style={{ justifyContent: "flex-end", transition: "none" }}
          >
            <ul className="navbar-nav navbar-right">
              <NavItem path="/home" name="Home" />
              <NavItem path="/competition" name="Competition" />
              <NavItem path="/winners" name="Winners" />

              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle"
                  href="#"
                  id="navbarDropdownMenuLink"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Pages
                </a>
                <div
                  className="dropdown-menu dropdown-menu-right"
                  aria-labelledby="navbarDropdownMenuLink"
                >
                  <Link className="dropdown-item" to="/">
                    <div style={{ color: "black" }}>About</div>
                  </Link>
                  <Link className="dropdown-item" to="/">
                    <div style={{ color: "black" }}>Contact</div>
                  </Link>
                  <Link className="dropdown-item" to="/">
                    <div style={{ color: "black" }}>Our Team</div>
                  </Link>
                </div>
              </li>
              <li className="nav-item">
                <Button
                  className="courses-btn"
                  variant="contained"
                  style={{ marginRight: "1%" }}
                  href="/courses"
                >
                  Courses
                </Button>
              </li>
              <li className="nav-item">
                {this.props.auth.token !== null ? (
                  <li className="nav-item dropdown">
                    <a
                      className="nav-link dropdown-toggle"
                      href="#"
                      id="navbarDropdownMenuLink"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      <span
                        className="fa fa-user-circle-o fa-2x"
                        style={{
                          color: "#e03268",
                          marginRight: 10,
                          marginTop: -10,
                        }}
                      ></span>
                    </a>
                    <div
                      className="dropdown-menu dropdown-menu-right"
                      aria-labelledby="navbarDropdownMenuLink"
                    >
                      <Link className="dropdown-item" to="/profile">
                        <div style={{ color: "black" }}>Profile</div>
                      </Link>
                      <Link
                        className="dropdown-item"
                        onClick={this.props.authLogout}
                        to="#"
                      >
                        <div style={{ color: "black" }}>Logout</div>
                      </Link>
                    </div>
                  </li>
                ) : (
                  <Link
                    to={{
                      pathname: "/login",
                      state: { modal: true },
                    }}
                    onClick={this.toggleNav}
                  >
                    <Button className="pinkbtn" variant="contained">
                      Login
                    </Button>
                  </Link>
                )}
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

export default Header;
