import * as actionTypes from "./actionTypes";

export const profileStart = () => ({
  type: actionTypes.PROFILE_START,
});

export const profileSuccess = (profile) => ({
  type: actionTypes.PROFILE_SUCCESS,
  profile: profile,
});

export const profileFail = (err) => ({
  type: actionTypes.PROFILE_FAIL,
  error: err,
});

export const profileEdit = (profile) => ({
  type: actionTypes.EDIT_PROFILE,
  profile: profile,
});

export const fetchProfile = () => (dispatch) => {
  dispatch(profileStart());

  const token = localStorage.getItem("token");

  return fetch("https://robofreaks-be.herokuapp.com/api/users/profile", {
    headers: {
      "Content-Type": "application/json",
      Authorization: "token " + token,
    },
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((profile) => {
      console.log(profile);
      dispatch(profileSuccess(profile));
    })
    .catch((error) => dispatch(profileFail(error.message)));
};

export const editProfile = (
  first_name,
  last_name,
  organization,
  country,
  state
) => (dispatch) => {
  const token = localStorage.getItem("token");

  return fetch("https://robofreaks-be.herokuapp.com/api/users/profile", {
    method: "PUT",
    headers: {
      "Content-Type": "application/json",
      Authorization: "Token " + token,
    },
    body: JSON.stringify({
      first_name: first_name,
      last_name: last_name,
      organization: organization,
      country: country,
      state: state,
    }),
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((profile) => dispatch(profileEdit(profile)))
    .catch((error) => dispatch(profileFail(error.message)));
};
