import React, { Component } from "react";
import Player from "./Player";
import "./css/Learn.css";
// import { course } from "./courseObj";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import { makeStyles } from "@material-ui/core/styles";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import PlayCircleFilledWhiteOutlinedIcon from "@material-ui/icons/PlayCircleFilledWhiteOutlined";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import { Button } from "@material-ui/core";
import QuizComponent from "./quiz";
import { withRouter } from "react-router-dom";
import { quiz } from "./quizObj";
import Typography from "@material-ui/core/Typography";
import Dialog from "@material-ui/core/Dialog";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import { useParams } from "react-router";

const useStyles = makeStyles((theme) => ({
  module: {
    fontSize: 30,
    fontWeight: "bold",
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  listItemText: {
    fontWeight: "bold",
    fontSize: 18,
  },
  selected: {
    backgroundColor: "#f6f3f1 !important",
    color: "black !important",
    boxShadow: "none !important",
    borderRadius: "0px !important",
  },
  appBar: {
    position: "relative",
    backgroundColor: "#06bfad",
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function RenderQuiz({ startQuiz, endQuiz, next, quizSubmission }) {
  const classes = useStyles();
  return (
    <div>
      <Dialog
        fullScreen
        open={startQuiz}
        onClose={endQuiz}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={endQuiz}
              aria-label="close"
            >
              <CloseIcon style={{ backgroundColor: "#06bfad" }} />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Sound
            </Typography>
          </Toolbar>
        </AppBar>
        <QuizComponent
          quiz={quiz}
          quizSubmission={quizSubmission}
          // quiz={this.state.course.modules[this.state.modIdx].content[
          //   this.state.cntIdx
          // ].source}
          next={next}
        />
      </Dialog>
    </div>
  );
}

function RenderContent({ content, onClickContent, moduleId, cntIdx, modIdx }) {
  const classes = useStyles();
  return ({ content } = content.map((cnt, i) => {
    return (
      <ListItem
        button
        key={i}
        data-id2={i}
        data-id1={moduleId}
        className={classes.nested}
        onClick={onClickContent}
        selected={i === cntIdx && moduleId === modIdx}
        classes={{ selected: classes.selected }}
      >
        <ListItemIcon>
          {cnt.done ? (
            <CheckCircleIcon
              style={{ color: "#06bfad", backgroundColor: "white" }}
            />
          ) : (
            <PlayCircleFilledWhiteOutlinedIcon
              style={{ backgroundColor: "white" }}
            />
          )}
        </ListItemIcon>
        <ListItemText primary={cnt.title} />
      </ListItem>
    );
  }));
}

function RenderModules({
  modules,
  onClick,
  open,
  onClickContent,
  modIdx,
  cntIdx,
}) {
  const classes = useStyles();
  if (modules) {
    return modules.map((module, i) => {
      return (
        <div key={i}>
          <ListItem divider button key={i} data-id={i} onClick={onClick}>
            <ListItemText
              primary={module.title}
              classes={{ primary: classes.listItemText }}
            />
            {open[i] ? (
              <ExpandLess style={{ backgroundColor: "white" }} />
            ) : (
              <ExpandMore style={{ backgroundColor: "white" }} />
            )}
          </ListItem>
          <Collapse in={open[i]} timeout="auto" unmountOnExit>
            <RenderContent
              modIdx={modIdx}
              cntIdx={cntIdx}
              moduleId={i}
              content={module.content}
              onClickContent={onClickContent}
            />
          </Collapse>
        </div>
      );
    });
  } else {
    return null;
  }
}

class Learn extends Component {
  constructor(props) {
    super(props);

    this.state = {
      course: this.props.courses.courseData,
      modIdx: 0,
      cntIdx: 0,
      open: this.props.courses.courseData.modules
        ? new Array(this.props.courses.courseData.modules.length).fill(false)
        : new Array(1).fill(false),
      attempt: 0,
      isSideNavOpen: true,
      startQuiz: false,
      visibility: "visible",
    };

    this.handleClick = this.handleClick.bind(this);
    this.changeContent = this.changeContent.bind(this);
    this.next = this.next.bind(this);
    this.prev = this.prev.bind(this);
    this.toggleSideNav = this.toggleSideNav.bind(this);
    this.reportWindowSize = this.reportWindowSize.bind(this);
    this.onSuccessData = this.onSuccessData.bind(this);
    this.startQuiz = this.startQuiz.bind(this);
    this.quizSubmission = this.quizSubmission.bind(this);
    this.endQuiz = this.endQuiz.bind(this);
  }

  componentDidMount() {
    console.log(this.props.location.pathname);
    // const search = this.props.location.search;
    // const params = new URLSearchParams(search);
    // const course = params.get("course");
    const course = this.props.location.pathname.split("/");
    console.log(course[2]);
    this.props.fetchCourseData(course[2], this.onSuccessData);
    window.addEventListener("resize", this.reportWindowSize);
    if (window.innerWidth < 1003) {
      this.setState({
        isSideNavOpen: false,
      });
    }
    let open = this.state.open;
    open[this.state.modIdx] = true;
    this.setState({
      open: open,
    });
    console.log(this.props.courses);
  }

  onSuccessData() {
    console.log(this.props.courses.courseData);
    this.setState({
      course: this.props.courses.courseData,
    });
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.reportWindowSize);
  }

  reportWindowSize() {
    if (window.innerWidth < 1003) {
      this.setState({
        isSideNavOpen: false,
      });
    } else {
      this.setState({
        isSideNavOpen: true,
      });
    }
  }

  handleClick(e) {
    let open = this.state.open;
    open[e.currentTarget.dataset.id] = !open[e.currentTarget.dataset.id];
    this.setState({
      open: open,
    });
  }

  changeContent(e) {
    let modIdx = Number(e.currentTarget.dataset.id1);
    let cntIdx = Number(e.currentTarget.dataset.id2);
    this.setState({
      modIdx: modIdx,
      cntIdx: cntIdx,
    });
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }

  next() {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    let moduleLength = this.state.course.modules.length;
    let contentLength = this.state.course.modules[this.state.modIdx].content
      .length;
    let open = this.state.open;
    if (this.state.cntIdx < contentLength - 1 && this.state.cntIdx >= 0) {
      open[Number(this.state.modIdx)] = true;
      this.setState({
        cntIdx: Number(this.state.cntIdx) + 1,
        open: open,
      });
      return;
    }
    if (this.state.modIdx < moduleLength - 1 && this.state.modIdx >= 0) {
      open[this.state.modIdx] = false;
      open[Number(this.state.modIdx) + 1] = true;
      this.setState({
        modIdx: Number(this.state.modIdx) + 1,
        cntIdx: 0,
        open: open,
      });
    }
  }

  prev() {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    let moduleLength = this.state.course.modules.length;
    let contentLength = this.state.course.modules[this.state.modIdx].content
      .length;
    let open = this.state.open;
    if (this.state.cntIdx <= contentLength - 1 && this.state.cntIdx !== 0) {
      open[Number(this.state.modIdx)] = true;
      this.setState({
        cntIdx: this.state.cntIdx - 1,
        open: open,
      });
      return;
    }
    if (this.state.modIdx <= moduleLength - 1 && this.state.modIdx !== 0) {
      open[this.state.modIdx] = false;
      open[this.state.modIdx - 1] = true;
      this.setState({
        cntIdx:
          this.state.course.modules[this.state.modIdx - 1].content.length - 1,
        modIdx: this.state.modIdx - 1,
        open: open,
      });
    }
  }

  toggleSideNav() {
    this.setState({
      isSideNavOpen: !this.state.isSideNavOpen,
    });
  }

  startQuiz(source) {
    // this.props.fetchQuizData(source, this.onSuccessQuiz);
    this.setState({
      startQuiz: true,
    });
  }

  // onSuccessQuiz() {
  //   this.setState({
  //     startQuiz: true,
  //   });
  // }

  endQuiz() {
    this.setState({
      startQuiz: false,
    });
  }

  quizSubmission() {
    this.setState({
      attempt: this.state.attempt + 1,
      startQuiz: false,
    });
  }

  render() {
    let nowShowing = this.state.course.modules
      ? this.state.course.modules[this.state.modIdx].content[this.state.cntIdx]
      : null;
    console.log(nowShowing);
    console.log(this.state.course);
    console.log(this.props.courses);
    return (
      <div className="learn container-fluid">
        {console.log(this.props.courses)}
        {console.log(this.state.course)}
        <div className="row row justify-content-between title border-bottom">
          <div className="col-6 col-md-8 col-lg-10 pt-3 align-items-center">
            <h4>{this.state.course ? this.state.course.title : null}</h4>
          </div>
          <div className="col-5 col-md-4 col-lg-2 align-items-center">
            <div className="row justify-content-end">
              <Button
                variant="text"
                onClick={this.prev}
                disabled={this.state.modIdx === 0 && this.state.cntIdx === 0}
              >
                <span
                  style={{ color: "#06bfad" }}
                  className="fa fa-arrow-circle-left fa-3x"
                ></span>
              </Button>
              <Button
                variant="text"
                onClick={this.next}
                disabled={
                  this.state.course.modules
                    ? this.state.modIdx ===
                        this.state.course.modules.length - 1 &&
                      this.state.cntIdx ===
                        this.state.course.modules[
                          this.state.course.modules.length - 1
                        ].content.length -
                          1
                    : true
                }
              >
                <span
                  style={{ color: "#06bfad" }}
                  className="fa fa-arrow-circle-right fa-3x"
                ></span>
              </Button>
            </div>
          </div>
        </div>
        <div className="row justify-content-start">
          <div
            className={
              this.state.isSideNavOpen
                ? "sidenav d-flex flex-row"
                : "sidenav-close d-flex flex-row"
            }
          >
            <div className="col-10 align-items-center scrollbar border menu">
              <div className="row justify-content-center">
                <div className="col-12">
                  <ul className="list-unstyled mb-30">
                    <List
                      component="nav"
                      aria-labelledby="nested-list-subheader"
                    >
                      <RenderModules
                        modIdx={this.state.modIdx}
                        cntIdx={this.state.cntIdx}
                        modules={this.state.course.modules}
                        onClick={this.handleClick}
                        open={this.state.open}
                        onClickContent={this.changeContent}
                      />
                    </List>
                  </ul>
                </div>
              </div>
            </div>
            <div
              className="col-2"
              style={{ backgroundColor: "#06bfad", height: 500 }}
            >
              <div className="row mt-2">
                <button
                  style={{
                    backgroundColor: "#06bfad",
                    outline: "none",
                    border: "none",
                    margin: "auto",
                  }}
                  type="button"
                  onClick={this.toggleSideNav}
                  onKeyDown={this.toggleSideNav}
                >
                  <span
                    style={{ color: "white" }}
                    className="fa fa-bars fa-lg"
                  ></span>
                </button>
              </div>
            </div>
          </div>
          <div
            className={
              this.state.isSideNavOpen
                ? "col align-items-center main p-3"
                : "col align-items-center main-closed p-3"
            }
          >
            <div className="row justify-content-center">
              <div className="col col-12">
                {this.state.course.modules ? (
                  <div>
                    {this.state.course.modules[this.state.modIdx].content[
                      this.state.cntIdx
                    ] ? (
                      <div>
                        {this.state.course.modules[this.state.modIdx].content[
                          this.state.cntIdx
                        ].content_type === "video" ? (
                          <div className="col col-12">
                            <div
                              style={{ position: "relative", marginBottom: 20 }}
                            >
                              <div className="heading">
                                <h6>
                                  {
                                    this.state.course.modules[this.state.modIdx]
                                      .content[this.state.cntIdx].title
                                  }
                                </h6>
                              </div>
                            </div>
                            <Player
                              url={
                                this.state.course.modules[this.state.modIdx]
                                  .content[this.state.cntIdx].source
                              }
                              vimeoId={
                                this.state.course.modules[this.state.modIdx]
                                  .content[this.state.cntIdx].vimeoId
                              }
                              next={this.next}
                              isSideNavOpen={this.state.isSideNavOpen}
                            />
                          </div>
                        ) : (
                          <div>
                            <div
                              className="quiz container"
                              style={{
                                marginTop: 100,
                                marginBottom: 40,
                                paddingLeft: 50,
                              }}
                            >
                              <div className="row justify-content-center">
                                <div className="col-12">
                                  <h2>
                                    {
                                      this.state.course.modules[
                                        this.state.modIdx
                                      ].content[this.state.cntIdx].title
                                    }
                                  </h2>
                                </div>
                                <hr />
                                <div
                                  className="col-12 col-sm-8"
                                  style={{ marginTop: 20 }}
                                >
                                  <h6>Submit your assignment</h6>
                                </div>
                                <div className="col-12 col-sm-4">
                                  <Button
                                    className="cyan"
                                    variant="contained"
                                    type="button"
                                    onClick={() => {
                                      this.startQuiz(
                                        this.state.course.modules[
                                          this.state.modIdx
                                        ].content[this.state.cntIdx].source
                                      );
                                    }}
                                  >
                                    {this.state.attempt > 0 ? "Retry" : "Start"}
                                  </Button>
                                </div>
                              </div>
                              <RenderQuiz
                                startQuiz={this.state.startQuiz}
                                endQuiz={this.endQuiz}
                                quizSubmission={this.quizSubmission}
                                next={this.next}
                              />
                            </div>
                          </div>
                        )}
                      </div>
                    ) : (
                      Error
                    )}
                  </div>
                ) : null}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Learn);
