import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { CircularProgress } from "@material-ui/core";

class VerifyMail extends Component {
  componentDidMount() {}

  render() {
    const search = this.props.location.search;
    const params = new URLSearchParams(search);
    const user_id = Number(params.get("user_id"));
    const timestamp = Number(params.get("timestamp"));
    const signature = params.get("signature");
    this.props.verifyEmail(user_id, timestamp, signature);
    return (
      <div className="container" style={{ marginTop: 70 }}>
        {this.props.auth.signup ? (
          <div>
            <h6>Verifying Your Email</h6>
            <CircularProgress animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </CircularProgress>
          </div>
        ) : (
          this.props.history.push("/")
        )}
      </div>
    );
  }
}

export default withRouter(VerifyMail);
