export const CourseDataStart = () => ({
  type: actionTypes.COURSEDATA_START,
});

export const CourseDataSuccess = (courseData) => ({
  type: actionTypes.COURSEDATA_SUCCESS,
  courseData: courseData,
});

export const CourseDataFail = (err) => ({
  type: actionTypes.COURSEDATA_FAIL,
  error: err,
});

export const fetchCourseData = (course_slug, onSuccessData) => (dispatch) => {
  dispatch(CourseDataStart());
  console.log(course_slug);
  const token = localStorage.getItem("token");

  return fetch(
    `https://robofreaks-be.herokuapp.com/api/courses/${course_slug}`,
    {
      headers: {
        "Content-Type": "application/json",
        Authorization: "token " + token,
      },
    }
  )
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((courseData) => {
      console.log(courseData);
      dispatch(CourseDataSuccess(courseData));
      onSuccessData();
    })
    .catch((error) => {
      console.log(error);
      dispatch(CourseDataFail(error.message));
    });
};

