import React, { Component } from "react";
import { Redirect, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { fetchProfile, editProfile } from "../redux/actions/profile";
import {
  fetchMyCourses,
  fetchCourseData,
  fetchQuizData,
} from "../redux/actions/course";

const mapStateToProps = (state) => {
  return {
    courses: state.courses,
    profile: state.profile,
  };
};

const mapDispatchToProps = (dispatch) => ({
  fetchProfile: () => dispatch(fetchProfile()),
  editProfile: (first_name, last_name, organization, country, state) =>
    dispatch(editProfile(first_name, last_name, organization, country, state)),
  fetchMyCourses: () => dispatch(fetchMyCourses()),
  fetchCourseData: (course_slug, onSuccessData) =>
    dispatch(fetchCourseData(course_slug, onSuccessData)),
  fetchQuizData: (source, onSuccessQuiz) =>
    dispatch(fetchQuizData(source, onSuccessQuiz)),
});

class ProtectedRoute extends Component {
  render() {
    const Component = this.props.component;
    const isAuthenticated = localStorage.getItem("token");
    // const isAuthenticated = true;
    return isAuthenticated ? (
      <div>
        <Component
          profile={this.props.profile}
          courses={this.props.courses}
          editProfile={this.props.editProfile}
          fetchProfile={this.props.fetchProfile}
          fetchMyCourses={this.props.fetchMyCourses}
          fetchCourseData={this.props.fetchCourseData}
          fetchQuizData={this.props.fetchQuizData}
        />
      </div>
    ) : (
      <Redirect to={{ pathname: "/login" }} />
    );
  }
}

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(ProtectedRoute)
);
