import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  error: null,
  loading: false,
  profile: {},
};

const profileStart = (state) => {
  return updateObejct(state, {
    error: null,
    loading: true,
    profile: null,
  });
};

const profileFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
    profile: null,
  });
};

const profileSuccess = (state, action) => {
  return updateObejct(state, {
    loading: false,
    error: null,
    profile: action.profile,
  });
};

const editProfile = (state, action) => {
  return updateObejct(state, {
    profile: action.profile,
  });
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PROFILE_START:
      return profileStart(state, action);
    case actionTypes.PROFILE_FAIL:
      return profileFail(state, action);
    case actionTypes.PROFILE_SUCCESS:
      return profileSuccess(state, action);
    case actionTypes.EDIT_PROFILE:
      return editProfile(state, action);
    default:
      return state;
  }
};

export default profileReducer;
